"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Profile from "@/components/Profile"; // Ensure this component exists

export default function ProfileAppPage() {
  const router = useRouter();
  const [userId, setUserId] = useState<string | null>(null);

  useEffect(() => {
    const storedUser = JSON.parse(localStorage.getItem("user") || "{}");

    if (!storedUser.id) {
      router.push("/login"); // Redirect if no user is found
      return;
    }

    setUserId(storedUser.id);
  }, []);

  if (!userId) return <p className="text-center mt-10 text-xl">Loading...</p>;

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100">
      <Profile userId={userId} />
    </div>
  );
}
